/**
 * Date_helpers
*/
class Date_helpers
{
	/**
	 * default_shown
	*/
	default_shown(value /*string in format 22.05.2023 or empty string*/)
	{
		/*
		^ asserts the start of the string.
		\d matches a digit (0-9).
		{1,2} specifies that the preceding pattern (digit) should occur between 1 and 2 times.
		\. matches the dot character literally.
		\d{4} matches exactly four digits.
		$ asserts the end of the string.
		*/
		let pattern = /^\d{1,2}\.\d{1,2}\.\d{4}$/;

		// set empty value if pattern does not match
		if (value !== '' && !pattern.test(value)) {
			value = '';
		}

		let now = {};
		if (value === '') {
			now = new Date();
		} else {
			value = value.split('.');
			now = new Date(value[2], value[1] - 1, value[0]);
		}

		return {
			day: now.getDate(),
			month: now.getMonth() + 1,
			year: now.getFullYear(),
			decade: (Math.floor(now.getFullYear() / 10) * 10) - 10,
		};
	}


	/**
	 * construct_days
	*/
	construct_days(year, month)
	{
		let last_day = new Date(year, month, 0).getDate();

		let days = [];
		var i;
		for (i = 1; i < last_day + 1; i++) {
			days.push(i);
		}

		let first_day = new Date(year, month - 1, 1).getDay();
		let add_empty = null;
		/*
		0 ne  6
		1 po  0
		2 ut  1
		3 str 2
		4 stv 3
		5 pi  4
		6 so  5
		*/
		if (first_day === 0) {
			add_empty = first_day + 6;
		} else {
			add_empty = first_day - 1;
		}

		for (i = 0; i < add_empty; i++) {
			days.unshift(0);
		}

		return days;
	}


	/**
	 * construct_week
	*/
	construct_week()
	{
		return [
			'Mon',
			'Tue',
			'Wed',
			'Thu',
			'Fri',
			'Sat',
			'Sun',
		];
	}


	/**
	 * construct_months
	*/
	construct_months()
	{
		return {
			January: 1,
			February: 2,
			March: 3,
			April: 4,
			May: 5,
			June: 6,
			July: 7,
			August: 8,
			September: 9,
			October: 10,
			November: 11,
			December: 12,
		};
	}


	/**
	 * show_month_name
	*/
	show_month_name(order)
	{
		let moon = '';
		let moons = this.construct_months();
		for (let key in moons) { if (!moons.hasOwnProperty(key)) { continue; }
			if (moons[key] === order) {
				return key;
			}
		}
		return 'no month found';
	}


	/**
	 * construct_years
	*/
	construct_years(decade)
	{
		let years = [];
		let i;
		for (i = decade; i < decade + 30; i++) {
			years.push(i);
		}

		return years;
	}
}

export default new Date_helpers;
